.background {
    width:100vw;
    z-index:0;
    margin-top:-58%;
    display:grid;
    position: relative;
}

.contentContainer {
    display:grid; 
    grid-template-columns:5% 90% 5%;
  
    height:30vw;
}

.charityHeader {
    grid-column:2;
    font-size:7vw;
    margin-top:-10%;
    z-index:3;
}

.textContainer {
    display:grid;
    grid-column:2;
    grid-template-columns:repeat(3, 33%);
    grid-template-rows:100%;
    grid-gap:2%;
}

.support{
    height:10vw;
    width:80%;
    display:grid;
    grid-gap:10%;
    grid-template-rows:5% 30% 50%;
    align-self:start;
    line-height: 175%;
}



